<template>
  <div>
    <span v-if="!isExpended" class="iconfont icon-search"
    @click="isExpended = true;$nextTick(() => {
      $refs.bar.focus()
    })" style="cursor:pointer;color:#aaa;"></span>

    <div v-else class="searchbar-wrapper flex-center">
      <i class="iconfont icon-search" style="color:#aaa;"></i>
      <el-input ref='bar' size="mini" :value="keyword" @input="changeKeyword">
        <div slot='append' class="flex-center el-icon-close"
        style="height: 100%; cursor: pointer; color:#aaa;"
        @click="changeKeyword(undifined);isExpended = false;"></div>
      </el-input>
    </div>
  </div>
</template>

<script>
import { defineComponent, ref, watch } from '@vue/composition-api';

export default defineComponent({
  props: {
    keyword: {
      type: String,
    },
  },
  setup(props, { root }) {
    const isExpended = ref(false);
    watch(() => props?.keyword, (newVal, oldVal) => {
      if (newVal && !oldVal) {
        isExpended.value = true;
      }
    }, {
      immediate: true,
    });

    function changeKeyword(val) {
      const { $route, $router } = root;
      if ($route.query.keyword) {
        $router.replace({
          query: { ...$route.query, keyword: val },
        });
      } else {
        $router.push({
          query: { ...$route.query, keyword: val },
        });
      }
    }
    return {
      isExpended,

      changeKeyword,
    };
  },
});
</script>

